.btn {
  display: flex;
  align-items:center;
  cursor:pointer;
  height: 36px;
  width: 100%;
  font-size: 14px;
  text-align: center;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  justify-content:center;
  font-weight: 600;
  border-radius: 4px;
  &--blue {
    background: #A1AFDB;
    color: #16181C;
  }
  &--circle {
    width: 56px;
    height: 56px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2), 0px 1px 10px rgba(0, 0, 0, 0.12), 0px 4px 5px rgba(0, 0, 0, 0.14);
    border-radius: 28px;
  }



  &--pins {
    width: 36px;
    height: 36px;

    flex-direction: row !important;
    justify-content: center !important;
    align-items:center !important;
    transform:translateY(3px);
    border-radius: 50%;
    transition-duration:.3s;
    margin-left: 8px;
    &:hover {
      background: rgba(255, 255, 255, 0.07);
      transition-duration:.3s;
    }
  }
}


.MuiFilledInput-underline, .MuiInput-root, .MuiFilledInput-root {
  &:after {
    border-color: #A1AFDB !important;
  }
}


.MuiPaper-root {
  background-color: #2c2f38 !important;
  margin-bottom: 20px;
  box-shadow: 0px 5px 5px rgba(12, 13, 15, 0.2), 0px 3px 14px rgba(12, 13, 15, 0.12), 0px 8px 10px rgba(12, 13, 15, 0.14);
  color: rgba(255,255,255,0.86) !important;
  .MuiListItem-root.Mui-selected, .MuiListItem-button:hover {
    background-color: rgba(255,255,255,0.07) !important;
  }
}

.MuiFilledInput-root {
  background-color: #434753 !important;
}

.MuiSelect-root  {

  color: rgba(255, 255, 255, 0.87) !important;

  span {
    width: 100%;
    height: 19px;
    display: block;
  }
}

.generateTable__btnsWrapper {
  margin-left: 10px;
}
.btn--table {
  background:#434753;
  height: 38px;
  width: 38px;
  margin-right: 8px;
  display: flex;
  flex-direction: row;
  justify-content: center !important;
  align-items:center;
  border-radius:50%;
}