.сustomStyle {
  margin-bottom: 60px;
  * {
    letter-spacing: 0px;
  }
  & > div {
    box-shadow: none;
  }

  .MuiPaper-root {
    background-color: transparent !important;

    * {
      color: rgba(255, 255, 255, 0.87);
    }

    .MuiInputAdornment-root.MuiInputAdornment-positionEnd, .MTableToolbar-spacer-35 {
      display: none !important;
    }
  }

  .MuiInput-underline:hover:not(.Mui-disabled):before {
    border-bottom: 2px solid transparent;
  }

  .MuiInput-underline:after {
    border-color: rgba(255, 255, 255, 0.38);
  }

  .MuiFormControl-root.MuiTextField-root.MTableToolbar-searchField-38 {
    width: 100%;
    padding-left: 0;
  }

  .MuiInput-underline:before {
    border-bottom: 2px solid transparent;
  }

  .MuiToolbar-root, .MuiToolbar-gutters {
    padding: 0 !important;
  }

  .MuiToolbar-root {
    margin-bottom: 20px;

    & > div:first-of-type {
      display: none;
    }

    .MuiFormControl-root {
      width: 100%;
      padding-left: 0;
    }
  }


  table.MuiTable-root {
    table-layout: fixed;
    border-radius: 8px 8px 1px 1px;

    overflow: hidden;
  }

  .red {
    color: #D8635B;

    svg {
      margin-right: 5px;
    }
  }

  .green {
    color: #519D64;

    svg {
      margin-right: 5px;
    }
  }

  span {
    font-size: 13px;
  }

  tr {
    border-bottom: 1px solid #4c4f56;
  }

  thead {
    background-color: #343843 !important;
    td {
      font-size: 13px;
      letter-spacing: 0.2px;

    }
    .doubled-row {
      td {
        padding-top: 10px;
        padding-bottom: 13px;
      }
    }
    th {
      color: white;
      border-bottom: 0px;
      font-size: 13px;
      padding: 2px 20px 0;
      opacity: 0.6;
      line-height: 1;
      height: 48px;
      font-weight: 300 !important;
      border-bottom: 1px solid rgba(255, 255, 255, 0.13);
      background-color: transparent !important;

      * {
        font-size: 13px;
        color: white !important;

        span {
          color: white !important;
        }
      }

      .material-icons {
        display: none;
      }
    }

    th:first-of-type {
      border-top-left-radius: 8px;
    }

    th:last-of-type {
      border-top-right-radius: 8px;
    }

  }

  tbody {
    background-color: #2c2f38 ;
    .date-row {
     td {
       padding-top: 8px;
       padding-bottom: 10px;
     }
    }
    td {
      font-size: 13px;
      padding-top: 12px;
      padding-bottom: 10px;
      font-weight: 300 !important;
      color: rgba(255, 255, 255, 0.6);
      white-space: nowrap; /* Запрещаем перенос строк */
      overflow: hidden; /* Обрезаем все, что не помещается в область */
      text-overflow: clip;
      height: 48px;
      b {
        margin-right: 2px;
        margin-left: 2px;
      }
      input {
        box-sizing: border-box;
        height: 37px !important;
      }
    }

    span, div {
      display: inline-flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center
    }
  }

  .licenseUsersTable {
    td {
      padding-top: 4px;
      padding-bottom: 5px;
    }
    .MuiSwitch-root {
      transform:translate(-10px);
    }
  }

  .shopTable {
    thead {
      th {
        padding-top: 20px;
        padding-bottom: 18px;
      }
    }
    tbody {
      td {
        padding-top: 9px;
        padding-bottom: 8px;
      }
    }
  }

  .MuiInputAdornment-root.MuiInputAdornment-positionStart {
    display: none;
  }


  .tableCell-profile {
    display: flex;
    flex-direction: row;
    margin-left: 12px;
    margin-top: 6px;
    div {
      text-align: left;
      margin-left: 12px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }

    h5 {
      font-size: 14px;
      margin: 0;
      line-height: 1;
      margin-bottom: 7px;
      margin-top: -3px;
      font-weight: 300;
      letter-spacing: 0.5px;
    }

    span {
      font-size: 11px;

      margin: 0;
      line-height: 1;
      color: rgba(255, 255, 255, 0.6);
    }
  }

  .tableCell-configLimits {
    display: flex;
    flex-direction: row;
  }

  .tableCell-daysLeft {
    display: inline-flex;
    flex-direction: row;
    background: #393c45;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;

    div {
      padding: 5px 0px 9px;
      line-height: 1;
      font-size: 13px;
      color: rgba(255, 255, 255, 0.6);
      width: 45px;
      text-align: center;
      display: inline-flex;
      justify-content: space-around;

      border-bottom: 1px solid rgba(255, 255, 255, 0);

      &:last-of-type {
        background: #595e6f63;
        border-top-right-radius: 4px;
        cursor: pointer;
        transition-duration: .3s;
      }
      &.btn {
        width: 36px;
      }
      &:first-of-type {
        font-size: 15px;
        color: rgba(255, 255, 255, 0.86);
      }
    }
  }

  .tableCell-configLimits {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    overflow: hidden;
    display: inline-flex;
    flex-direction: row;
    background: #3f414d;

    div {
      padding: 8px 0px 12px;
      line-height: 1;
      font-size: 13px;
      color: rgba(255, 255, 255, 0.6);
      width: 44px;
      text-align: center;
      display: flex;
      justify-content: space-around;
      border-bottom: 1px solid rgba(255, 255, 255, 0.38);
      align-self: normal;

      &:first-of-type {
        font-size: 15px;
        color: rgba(255, 255, 255, 0.86);
      }

      &:nth-of-type(2) {
        padding-top: 10px;
      }

    }

    .btn--configLimits {
      background: #595e6f63;
      margin-right: 1px;
      align-self: normal;
      width: 36px;
      padding: 10px 0px 10px;
    }
  }
}


.icon__add {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: #A1AFDB;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2), 0px 1px 10px rgba(0, 0, 0, 0.12), 0px 4px 5px rgba(0, 0, 0, 0.14);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.corporate__users__panel {
  margin-bottom: -56px;
  justify-content: flex-end;
  display: flex;
  position: relative;
  z-index: 11;
  width: 330px;
  float: right;
  margin-top: 4px;
}

.tabItem__arrow {
  padding-left: 0;
  padding-right: 0;
  cursor: pointer;
  margin-right: 15px;
}

#corporate__users__table {
  .MuiToolbar-root .MuiFormControl-root {
    width: calc(100% - 80px);
  }
}


#message__table {
  .MuiToolbar-root {
    display: none;
  }

  thead {
    th {
      overflow: hidden;

      &:nth-of-type(1) {
        width: 15% !important;
      }

      &:nth-of-type(2) {
        width: 61% !important;
      }

      &:nth-of-type(3) {
        width: 12% !important;
      }

      &:nth-of-type(4) {
        width: 12% !important;
      }
    }
  }

  tbody {
    tr {
      cursor: pointer;
    }

    td {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;

      &:nth-of-type(1) {
        color: $colorLight !important;
      }

      &:nth-of-type(2) {
        color: $colorLight !important;

        .author {
          font-size: inherit;
          color: inherit;
          display: inline-block;
          color: $colorDark !important;
          margin-right: 5px;
        }
      }

    }
  }
}


.table_checkbox {
  width: 55px !important;
  padding: 0 !important;
  opacity: 1 !important;
  padding-left: 6px !important;
  & > span {
    padding: 8px;
  }

  span {
    font-size: 20px !important;
    display: inline-block !important;

    &.MuiIconButton-label {
      width: 26px;
      height: 26px;
    }

    svg {
      font-size: 20px !important;
      margin-top: 5px;
      fill: rgba(255, 255, 255, 0.6) !important;
    }
  }

  .Mui-checked {
    svg {
      fill: #A1AFDB !important;
    }
  }
}

table .MuiCheckbox-root {
  svg {
    font-size: 22px !important;
    fill: rgba(255, 255, 255, 0.6) !important;
  }

  .Mui-checked {
    svg {
      fill: #A1AFDB !important;
    }
  }
}

