  white-space: normal !important;
}

.position-relative {
  position: relative;
}

.text-align-right {
  text-align: right !important;
}

.align-items-baseline {
  align-items: baseline !important;
}


tbody .padding-sides {
  &-15 {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      height: 100%;
      background: rgb(44, 47, 56);
      background: linear-gradient(90deg, rgba(44, 47, 56, 0) 0%, rgba(44, 47, 56, 1) 100%);
      width: 15px;
      right: 0;
      top: 0;
    }
  }
}

tbody tr {
  cursor: pointer;

  td {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      height: 100%;
      background: rgb(44, 47, 56);
      background: linear-gradient(90deg, rgba(44, 47, 56, 0) 0%, rgba(44, 47, 56, 1) 100%);
      width: 15px;
      right: 0;
      top: 0;
    }
  }



  &.active {
    background: rgba(98, 121, 195, 0.17);
    .MuiCheckbox-root svg {
      font-size: 22px !important;
      fill: #A1AFDB !important;
    }

    td {
      position: relative;
      background: rgba(60, 66, 86, 1);

      &:after {
        content: '';
        position: absolute;
        height: 100%;
        background: rgb(2, 0, 36);
        background: linear-gradient(90deg, rgba(2, 0, 36, 0) 0%, #3c4256 100%);
        width: 15px;
        right: 0;
        top: 0;
      }
    }
  }
}


.table_action_bar {
  position: fixed;
  bottom: 0;
  width: calc(100vw - 280px);
  background: #434753;
  box-shadow: 0px -3px 5px rgba(12, 13, 15, 0.2), 0px -1px 18px rgba(12, 13, 15, 0.12), 0px -6px 10px rgba(12, 13, 15, 0.14);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  height: 64px;

  &--shop {
    & > div:nth-of-type(1) {
      margin-right: auto;
    }
  }

  & > div {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  &--license {
    p {
      font-size: 14px;
      margin: 0;
      margin-left: 20px;
    }

    .MuiPaper-root {
      background-color: #2c2f38 !important;
    }



    .MuiFormControl-root {
      margin-right: 10px !important;
    }

    .MuiInputBase-root {
      overflow: hidden;
      border-radius: 4px 4px 0px 0px;
    }

    .MuiSelect-root {
      background: #2C2F38;

      padding-top: 10px;

      &:focus {
        background: #2C2F38 !important;
      }
    }

  }

  .btn {
    padding-left: 15px;
    padding-right: 15px;
    font-size: 14px;
    letter-spacing: 1.25px;
    display: inline-flex;
    width: auto;
    line-height: 1;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    &--cancel {
      padding-left: 11px;
      padding-right: 11px;
    }
    &--transparent {
      border: 2px solid #C1C4CD;
      color: rgba(255, 255, 255, 0.86);
    }
  }

  &__info {
    margin-right: 20px;
    color: white;
    font-size: 13px;
    letter-spacing: 1px;
    b {

      color: white;
      font-size: 14px;
      font-weight: bold;
    }
  }
}

.icon-inside {

  svg {
    transform: translateY(3px);
    margin-right: 8px;
  }
  &--right {
    svg {
      transform: translateY(3px);
      margin-left: 8px;
      margin-right: 0;
    }
  }
}

.date-row {
  background: #323541;

  &:hover {
    background: #323541;
  }

  td:after {
    display: none;
  }
}


.configTable {
  .MuiCheckbox-root {
    margin-right: -12px;
  }
}


.alertPanel {
  background: rgba(67, 71, 83, 0.3);
  border: 1px dashed #595E6F;
  padding: 15px 20px;
  border-radius: 8px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items:center;
  p {
    margin: 0;
    font-size: 14px;
  }
  .btn-text {
    font-size: 14px;
    line-height: 1;
    font-weight: 600;
    letter-spacing: 1.25px;
  }
}

.inputs-row {
  .MuiFilledInput-input {
    padding: 10px 12px;
  }
}

