@import 'vars';
// ==================== TYPOGRAPHY ========================= //


@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@400;500;600&display=swap');


body * {
  font-family: 'Open Sans', sans-serif ;
  color: $colorDark;
  letter-spacing:-0.2px;
  -webkit-text-size-adjust: none;
}
.font--exo2 {
  font-family: 'Exo 2', sans-serif;
}

h1 {
  font-size: 36px;
  font-weight: 500;
  line-height: 0.9;
  font-family: 'Exo 2', sans-serif;
}
h2 {
  font-size: 28px;
  font-weight: 300;
  letter-spacing: 0.3px;
  line-height: 0.9;
  font-family: 'Exo 2', sans-serif;
  color: white;
  letter-spacing: 0.1px;
  color: $colorLight;
}
h3 {
  font-size: 24px;
  font-weight: 400;
  line-height: 0.9;
}
h4 {
  font-weight: 400;
  letter-spacing: 0.3px;
}
body, p, span, div {
  font-size: 15px;
  line-height: 22px;
  font-weight: 300;
  letter-spacing: 0.1px;

}

b {
  font-weight: bold;
  font-size: inherit !important;
}

.center {
  text-align: center;
}

@media screen and (max-width: 767px) {
  h1 {
    font-size: 30px;
  }
  h2 {
    font-size: 24px;
  }
  h3 {
    font-size: 18px;
  }
  body, p, span, div {
    font-size: 14px;
    line-height: 21px;
  }
}

// ==================== TYPOGRAPHY END ========================= //